body {
  margin: 0;
  /* font-family:  */
}

.navigation {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 56px;
  padding: 0 5%;
  background: #333;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation ul {
  list-style: none;
  padding: 0;
  color: white;
}

.nabigation ul li:hover {
  cursor: pointer;
}

.navigation a {
  text-decoration: none;
}

main {
  display: flex;
  margin-top: 56px;
  max-height: calc(100vh - 56px);
}

.leftContent {
  display: flex;
  flex-basis: 50%;
  flex-direction: column;
  align-items: center;
  background-color: #282c34;
  height: calc(100vh - 56px);
}

.leftContent img {
    width: 40%
}

.leftContent form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 10px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
  width: 200px;
  margin: 50px auto 0 auto;
  background: #333;
  padding: 1.5em;
}
